@import 'index.css';
@import 'Nav.css';
@import 'Nav-Modal.css';
@import 'About.css';
@import 'Header.css';
@import 'Portfolio.css';
@import 'Contact.css';

.App {
  text-align: center;
  background: linear-gradient(45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	/*animation: gradient 15s ease infinite;*/

}
.App-logo {
  max-height: 40vmin;
  pointer-events: none;
}

.Main-App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.App-link {
  color: #61dafb;
}

.Anchor-Tag-Section{
	margin-bottom:5rem;
}


@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


